/* global styles */
html,
body {
  height: 100vh;
  overflow: hidden;
  margin: 0;
}
#app {
  font-family: 'Open Sans', Helvetica, Arial, sans-serif;
  font-size: 20px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  height: 100vh;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: #181919 linear-gradient(30deg, #1c403d, #181919);
}
div.dg.ac {
  z-index: 1000;
}




